.App{
  text-align: center;
}

.box{
  max-width: 200px;
  
  margin: 50px auto;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  padding: 10px;
  background-color: gold;
}

p{
  margin-top: 20px;
  font-size: 50px;
  text-align: center;
}

button{
  
  padding: 10px;
  margin: 20px 10px;
  font-size: 16px;
  color: #FFFFFF;
  border: 0px;
  border-radius: 5px;
  cursor: pointer;
}

button.add{
  background-color: green;
  max-width: 100px;
}

button.sub{
  background-color: blue;
  max-width: 100px;
}

button.Reset{
  background-color: black;
}

button.reset{
  background-color: #0F3460;
}

.Logo{
  height:100px;
}